import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main_Component'
import Wee from '@/components/Wee_Component'
import Dor_Taipai from '@/components/Dor_Taipai_Component'
import Dor_Melbourne from '@/components/Dor_Melbourne_Component'
import Ourstory_Project from '@/components/Ourstory_Project_Component'
import Ourstory_Wearound from '@/components/Ourstory_Wearound_Component'
import Ourstory_With from '@/components/Ourstory_With_Component'
import Village from '@/components/Village_Component'
import Shop from '@/components/Shop_Component'
import About from '@/components/About_Component'
import Join from '@/components/Join_Component'
import Login from '@/components/Login_Component'

Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component: Main
    },
    {
      path: '/wee',
      name: 'wee',
      component: Wee
    },
    {
      path: '/dor_taipai',
      name: 'dor_taipai',
      component: Dor_Taipai
    },
    {
      path: '/dor_melbourne',
      name: 'dor_melbourne',
      component: Dor_Melbourne
    },
    {
      path: '/ourstory_project',
      name: 'ourstory_project',
      component: Ourstory_Project
    },
    {
      path: '/ourstory_wearound',
      name: 'ourstory_wearound',
      component: Ourstory_Wearound
    },
    {
      path: '/ourstory_with',
      name: 'ourstory_with',
      component: Ourstory_With
    },
    {
      path: '/village',
      name: 'village',
      component: Village
    },
    {
      path: '/shop',
      name: 'shop',
      component: Shop
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/join',
      name: 'join',
      component: Join
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})



