<script>
export default {
  mounted() {
    $(".box_pagination > span").click(function(){
      if( $(this).hasClass("current") == false ){
        $(".box_pagination > span").removeClass("current");
        $(this).addClass("current");
      }else{
        $(this).removeClass("current");
      }
      return false;
    });
  },
