            <input id="policy_agree" type="checkbox">
            <label for="policy_agree">자동로그인</label>
          </div>
          <button class="join_btn" type="submit" name="button">로그인</button>
          <div class="naver">
            <img src="../assets/img/main/naver.png" alt="">
            <p>네이버 아이디로 로그인 </p>
          </div>
          <p>비밀번호 찾기</p>
          <p>회원가입</p>
        </div>

      </div>
    </section>
  </main>
</template>

<script>
export default {
  name: 'login_component',
  data () {
    return {

    }
  }
}

</script>

