<template>
  <main id="container">
    <section id="join">
      <div class="inner">
        <div class="box">
          <div class="user_txt">
            <label for="id_value">이메일*</label>
            <input id="id_value" type="email">
            <label for="pw_value">비밀번호*</label>
            <input id="pw_value" type="password">
          </div>
          <div class="policy">
            <label for="policy_agree">이용약관 및 개인정보 처리방침에 동의*</label>
            <input id="policy_agree" type="checkbox">
          </div>
          <button class="join_btn" type="submit" name="button">회원가입</button>

        </div>
      </div>
    </section>
  </main>
</template>

<script>
export default {
  name: 'join_component',
  data () {
    return {

    }
  }
}

</script>

